let demo = document.querySelector("#html");
let string = `你好，
我是一名前端新人
这是我第一次尝试制作的一份动态页面`;
let string2 = '';
let n = 0;

let step = () => {
    setTimeout( ()=>{
        if (string[n] === "\n"){
            string2 += "<br>";
        } else {
            string2 += string[n];
        }
        demo.innerHTML = string2;
        if (n +1 < string.length) {
            n+= 1;
            step();
        }
    }, 100);

};
step();
